import EventEmitter from "eventemitter3";

interface Listener<T> {
    (arg: T): void;
}

export type Event<T> = {
    on: (listener: Listener<T>) => Listener<T>;
    once: (listener: Listener<T>) => Listener<T>;
    off: (listener: Listener<T>) => Listener<T>;
    emit: (arg: T) => void;
}

export function createEvent<T>(eventEmitter: EventEmitter, key: string): Event<T> {
    return {
        on: listener => {
            eventEmitter.on(key, listener);
            return listener;
        },
        once: listener => {
            eventEmitter.once(key, listener);
            return listener;
        },
        off: listener => {
            eventEmitter.off(key, listener);
            return listener;
        },
        emit: arg => eventEmitter.emit(key, arg),
    };
}
