import { getRoutePathUrl } from "../router/routePathUrl";
import { Trello } from "../types/trello";

export function getCardBackSection(t: Trello.PowerUp.IFrame, props: CapabilityProps): Trello.PowerUp.CardBackSection {
    return {
        title: "Estimate",
        icon: props.icon.dark,
        content: {
            type: "iframe",
            url: t.signUrl(getRoutePathUrl("/card-back-section", { absolute: true })),
            height: 210,
        },
    };
}
