import { minutesToMs, msToMinutes } from "./timeParsing";

export function addMinutesToDate(date: Date, minutes: number) {
    return new Date(date.getTime() + minutesToMs(minutes));
}

export function getMsBetween(earlierDate: Date, laterDate: Date) {
    return laterDate.getTime() - earlierDate.getTime();
}

export function getMinutesBetween(earlierDate: Date, laterDate: Date) {
    return msToMinutes(getMsBetween(earlierDate, laterDate));
}

export function getElapsedMs(date: Date) {
    return Date.now() - date.getTime();
}

export function getElapsedSeconds(date: Date) {
    return getElapsedMs(date) / 1000;
}

export function getElapsedMinutes(date: Date) {
    return getElapsedMs(date) / 1000 / 60;
}

export function areDatesEqual(date1: Date, date2: Date) {
    return date1?.getTime() === date2?.getTime();
}
