import { getRoutePathUrl } from "../router/routePathUrl";
import { Trello } from "../types/trello";

export function getBoardButton(_t: Trello.PowerUp.IFrame, props: CapabilityProps): Trello.PowerUp.BoardButtonCallback[] {
    return [{
        icon: {
            dark: props.icon.dark,
            light: props.icon.light,
        },
        text: "Estimate",
        callback: (tc: Trello.PowerUp.IFrame) => {
            return tc.modal({
                title: "Estimate",
                url: tc.signUrl(getRoutePathUrl("/board-page")),
                fullscreen: false,
                height: 400,
            });
        },
        condition: "edit",
    }];
}
