import { CurrentWork, Estimate, getTotalWorkMinutes } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { getElapsedMinutes } from "./dateMath";
import { formatMinutes } from "./timeParsing";

type Args = {
    cardId: Trello.PowerUp.Card["id"] | undefined,
    estimate: Estimate,
    totalWorkMinutes?: number,
    currentWork?: CurrentWork | null | undefined,
}

export function getWorkDelta(args: Args) {
    let totalWorkMinutes = args.totalWorkMinutes ?? getTotalWorkMinutes(args.estimate);

    if (args.currentWork != null && args.currentWork.cardId === args.cardId)
        totalWorkMinutes += getElapsedMinutes(args.currentWork.startDate);

    return args.estimate.estimateMinutes - totalWorkMinutes;
}

type FormattedArgs = {
    delta: number,
    short?: boolean,
}

export function formatWorkDelta({ delta, short }: FormattedArgs) {
    if (delta >= 0) {
        if (short)
            return "S: " + formatMinutes(delta);
        else
            return "Saved: " + formatMinutes(delta);
    }
    else {
        if (short)
            return "S: -" + formatMinutes(-delta);
        else
            return "Spent: " + formatMinutes(-delta);
    }
}
