import { formatDate } from "./dateFormatting";

const key = "exit.datetime";

export function addListenerToSaveExitTime() {
    window.addEventListener("pagehide", e => {
        localStorage.setItem(key, JSON.stringify(new Date()));
        // localStorage.setItem("pagehide", JSON.stringify(new Date()));
    });

    // window.addEventListener("visibilitychange", e => {
    //     localStorage.setItem("visibilitychange." + document.visibilityState, JSON.stringify(new Date()));
    // });

    const lastExitTime = getLastExitTime();
    console.log(key + ": " + (lastExitTime ? formatDate(lastExitTime, "yyyy-MM-dd hh:mm:ss") : "null"));
}

export function getLastExitTime() {
    const saved = JSON.parse(localStorage.getItem(key) as string);

    if (saved)
        return new Date(saved);

    return null;
}
